.params {
  display: flex;
  flex-direction: column;

  width: 337px;
}

.params__header {
  margin-bottom: 22px;

  font-size: 16px;
  line-height: 20px;
}

.params__tooltip,
.params__icon-question {
  width: 24px;
  height: 24px;
}

.params__tooltip {
  position: relative;
  left: 6px;
  top: 8px;
}

.params__select {
  box-sizing: border-box;
  max-width: 250px;
  width: 100%;
  height: 50px;

  padding: 0 20px;
  margin-bottom: 27px;

  font-size: 14px;
  line-height: 17px;
  color: $dustyGray;

  border: 1px solid #f6f6f6;
  border-radius: 10px;
  box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.1);

  background-color: $white;
  background-image: url("../img/svg/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 12px 7px;
  background-position: top 49% right 19px;

  &:hover {
    cursor: pointer;
  }
}

.params label {
  position: relative;
  padding-left: 35px;

  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    box-sizing: border-box;

    width: 20px;
    height: 20px;

    border: 2px solid $fountainBlue;
    border-radius: 50%;
    background-color: $blackSqueeze;
  }

  &:hover {
    cursor: pointer;
  }
}

.params label:not(:last-child) {
  margin-bottom: 21px;
}

.params input:checked + label {
  &::after {
    content: "";

    position: absolute;
    top: 4px;
    left: 4px;

    box-sizing: border-box;

    width: 12px;
    height: 12px;

    border-radius: 50%;
    background-color: $fountainBlue;
  }
}

.params input:focus + label {
  color: $fountainBlue;
}

@media (max-width: $desktop-l-width) {
  .params {
    width: 320px;
  }
}

@media (max-width: $desktop-m-width) {
  .params {
    width: 233px;
  }
}

@media (max-width: $tablet-width) {
  .params {
    width: 203px;
  }

  .params__header {
    margin-bottom: 17px;

    font-size: 12px;
    line-height: 15px;
  }

  .params__tooltip,
  .params__icon-question {
    width: 17px;
    height: 17px;
  }

  .params__tooltip {
    top: 6px;
    left: 10px;
  }

  .params__select {
    height: 37px;
    max-width: 170px;
    margin-bottom: 20px;

    padding: 0 15px;

    font-size: 10px;
    line-height: 12px;
  
    background-size: 8px 5px;
    background-position: top 50% right 14px;
  }

  .params label {
    box-sizing: border-box;
    width: 170px;

    margin-bottom: 10px;
    padding-left: 25px;

    font-size: 12px;
    line-height: 15px;

    &::before {
      width: 15px;
      height: 15px;
    }
  }

  .params label:not(:last-child) {
    margin-bottom: 14px;

  }

  .params input:checked + label {
    &::after {
      top: 4px;
      left: 4px;
  
      width: 7px;
      height: 7px;
    }
  }
}

@media (max-width: $mobile-width) {
  .params,
  .params__select {
    max-width: 100%;
    width: 100%;
  }

  .params {
    margin-bottom: 22px;
  }

  .params label {
    width: 100%;
  }

  .params__tooltip {
    top: 4px;
  }
}

