.compare-table {
  border-spacing: 0;
  border-collapse: collapse;
  border: none;
  text-align: left;
}

.compare-table th {
  padding: 0;
}

.compare-table__header-row {
  border-bottom: 1px solid $mercury
}

.compare-table__row-header {
  padding: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: left;

  vertical-align: top;
}

.compare-table td {
  width: 421px;

  padding: 0;
  margin: 0;
  vertical-align: top;
}

.compare-table .compare-table__header {
  padding-bottom: 30px;
}

.compare-table__item {
  width: 100%;
  max-width: 50%;
}