html * {
  &:focus {
    outline: 1px solid $fountainBlue;
  }
}

body {
  font-family: "Montserrat", Verdana, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: $black;
  line-height: 1.5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

p {
  margin: 0;
}

select {
  appearance: none;
}