.container {
  display: flex;
  flex-direction: column;

  max-width: $content-width-l;

  margin: 0 auto;

  box-shadow: 0px 29px 30px -22px rgba(0, 0, 0, 0.15);

  &--no-shadow {
    box-shadow: none;
  }
}

@media (max-width: $desktop-l-width) {
  .container {
    max-width: $content-width-m;
  }
}

@media (max-width: $desktop-m-width) {
  .container {
    max-width: $content-width-s;
  }
}

@media (max-width: $tablet-width) {
  .container {
    max-width: $content-width-xs;
  }
}

@media (max-width: $mobile-width) {
  .container {
    box-shadow: none;
    max-width: $content-width-xxs;
  }
}