.tooltip {
  width: 24px;
  height: 24px;
  padding: 0;

  border: none;
  background-color: transparent;
  cursor: pointer;
}

.tooltip__text {
  display: none;

  position: absolute;
  bottom: 30px;
  left: 12px;

  box-sizing: border-box;
  width: 258px;
  height: auto;

  padding: 20px;
  
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: left;

  border-radius: 14px;
  background-color: $white;
  filter: drop-shadow(0 20px 10px $dustyGray);
  transform: translateX(-50%);

  &::before {
    content: "";
    position: absolute;
    bottom: -1px;
    left: 50%;

    display: block;
    width: 0;
    height: 0;

    border: 6px solid $white;
    border-right-color: transparent;
    border-top-color: transparent;

    transform: rotate(-45deg) translateX(-50%);
  }
}

.tooltip:hover > .tooltip__text,
.tooltip:focus > .tooltip__text {
  display: inline-block;
}

@media (max-width: $tablet-width) {
  .tooltip,
  .tooltip__icon-question {
    width: 17px;
    height: 17px;
  }
}