.table {
  padding-top: 49px;
  margin-bottom: 50px;
}

.table__row {
  display: flex;
  margin-bottom: 37px;

  &--l-margin {
    margin-bottom: 45px;
  }

  &--sub {
    flex-direction: column;
    margin-bottom: 48px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.table__sub-row {
  display: flex;
  margin-bottom: 27px;
}

.table__sub-row:last-child {
  margin-bottom: 0;
}

.table__title,
.table__group-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
}

.table__title,
.table__sub-title {
  width: 337px;
  margin-right: 20px;
}

.table__group-title {
  margin-bottom: 30px;
}

.table__sub-title {
  margin-right: 10px;
  padding-left: 10px;

  font-size: 14px;
  line-height: 17px;
  color: $dustyGray;
}

.table__sub-title span:last-child {
  display: none;
}

.table__params,
.table__sub-params {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;

  max-width: 1040px;
  padding-top: 3px;
}

.table__sub-params {
  padding-top: 0;
}

.table__param,
.table__sub-param {
  width: 425px;
  margin-right: 35px;

  font-size: 14px;
  line-height: 17px;
}

.table__bonus {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  height: 27px;

  padding-right: 5px;
  padding-left: 32px;

  font-weight: 700;
  font-size: 12px;
  line-height: 27px;

  border: 2px solid $wildSand;
  border-radius: 5px;

  background-color: $white;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  &::before {
    content: "";
    position: absolute;
    left: 12px;
    top: 50%;
    width: 15px;
    height: 14px;

    background-image: url("../img/svg/icon-bonus.svg");
    background-position: center center;
    background-size: 15px 14px;
    background-repeat: no-repeat;

    transform: translateY(-50%);
  }
}

.table__tooltip-wrapper {
  display: inline-block;
  vertical-align: bottom;

  position: relative;
  width: 1px;
  height: 100%;
}

.table__tooltip {
  position: absolute;
  top: -6px;
  left: 18px;
}

.table__arrow {
  padding: 0;

  width: 13px;
  height: 8px;

  font-size: 0;

  border: none;
  background-color: transparent;
}

@media (max-width: $desktop-l-width) {
  .table {
    padding-top: 40px;
  }

  .table__sub-row {
    &--s-margin {
      margin-bottom: 20px;
    }
  }

  .table__title,
  .table__sub-title {
    width: 320px;
  }

  .table__param,
  .table__sub-param {
    max-width: 290px;
    margin-right: 20px;
  }
}

@media (max-width: $desktop-m-width) {
  .table__row,
  .table__sub-row {
    &--power {
      margin-bottom: 32px;
    }
  }

  .table__param,
  .table__sub-param {
    max-width: 232px;
    margin-right: 0;
  }

  .table__params,
  .table__sub-params {
    padding-top: 0;
  }

  .table__title,
  .table__sub-title {
    width: 240px;
  }

  .table__tooltip {
    left: 8px;
  }
}

@media (max-width: $tablet-width) {
  .table {
    margin-bottom: 33px;
  }

  .table__title,
  .table__group-title {
    font-size: 16px;
    line-height: 20px;
  }

  .table__title {
    position: relative;
    width: 180px;
  }

  .table__sub-title {
    position: relative;
    box-sizing: border-box;
    width: 180px;

    margin-right: 20px;
    padding-right: 20px;

    font-size: 12px;
    line-height: 15px;
  }

  .table__param,
  .table__sub-param {
    padding-top: 3px;
    width: 175px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px
  }

  .table__param--bonus {
    padding-top: 0;
  }

  .table__row {  
    margin-bottom: 37px;
    &--l-margin {
      margin-bottom: 32px;
    }

    &--tablet-margin {
      margin-bottom: 45px;
    }

    &--sub {
      margin-bottom: 52px;
    }
  }

  .table__sub-row {
    margin-bottom: 20px;

    &--manufacturer {
      margin-bottom: 27px; 
    }
  }

  .table__sub--display .table__sub-row:not(:last-child) {
    margin-bottom: 25px;
  }

  .table__sub--camera .table__sub-row {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .table__sub--camera .table__sub-row--resolution {
    margin-bottom: 20px;
  }

  .table__row--camera {
    margin-bottom: 45px;
  }

  .table__sub--power .table__sub-title {
    padding-right: 0;
  }

  .table__row--power {
    margin-bottom: 40px;
  }

  .table__sub--connect .table__sub-title {
    padding-right: 50px
  }

  .table__tooltip-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    width: 100%;
  }

  .table__tooltip {
    left: unset;
    right: 20px;
    top: 2px;
  }
}

@media (max-width: $mobile-width) {
  .table {
    padding-top: 25px;
  }

  .table__row,
  .table__sub-row {
    flex-direction: column;
  }

  .table__row {
    margin-bottom: 24px;
    padding-bottom: 27px;

    border-bottom: 2px solid $mercury;

    &--l-margin {
      padding-bottom: 27px;
    }

    &:last-child {
      border-bottom: none;
      padding-bottom: 0;
    }
  }

  .table__group-title,
  .table__title {
    width: 100%;
    margin: 0;
    margin-bottom: 10px;

    font-size: 14px;
    line-height: 17px;
  }
  
  .table__title-text {
    position: relative;
    display: inline-block;
    max-width: 170px;

    &::before {
      position: absolute;
      top: 5px;
      right: -28px;

      content: "";

      width: 13px;
      height: 8px;

      background-image: url("../img/svg/icon-arrow-up.svg");
      background-position: center center;
      background-repeat: no-repeat;
    }
  }

  .table__bonus {
    height: 22px;

    padding-right: 10px;
    padding-left: 30px;

    font-size: 10px;
    line-height: 22px;

    border-radius: 3px;
    box-shadow: 0px 2.63514px 3.51351px rgba(0, 0, 0, 0.02);

    &::before {
      height: 13px;
      width: 12px;

      background-size: 13px 12px;
    }
  }

  .table__param {
    font-size: 10px;
    line-height: 12px;
    width: 130px;

    &--bonus {
      padding-top: 5px;
      margin-bottom: 5px;
    }
  }

  .table__sub-title {
    display: flex;
    justify-content: space-between;
    width: 100%;

    margin: 0;
    margin-bottom: 8px;
    padding: 0;

    font-size: 10px;
    line-height: 12px;
  }

  .table__sub-title span {
    width: 130px;
  }

  .table__sub-title span:last-child {
    display: block;
  }

  .table__group-title {
    margin-bottom: 12px;
  }

  .table__sub-param {
    width: 130px;

    font-size: 10px;
    line-height: 12px;
  }

  .table__sub-row {
    margin-bottom: 9px;
  }

  .table__sub--display .table__sub-row:not(:last-child) {
    margin-bottom: 9px;
  }

  .table__sub--camera .table__sub-row {
    margin-bottom: 9px;
  }

  .table__row--camera .table__group-title {
    margin-bottom: 16px;
  }

  .table__tooltip {
    display: none;
  }

  .table__sub--connect .table__sub-title {
    padding-right: 0;
  }
}