.compare-item {
  position: relative;
  display: flex;

  max-width: 425px;
  width: 100%;

  padding-top: 30px;
  margin-right: 30px;
}

.compare-item__image-link {
  display: flex;
  justify-content: center;
  margin-right: 15px;

  width: 147px;
  height: 147px;
}

.compare-item__content {
  display: flex;
  flex-direction: column;

  max-width: 280px;
}

.compare-item__description {
  display: flex;
  flex-direction: column;
}

.compare-item__category {
  margin-bottom: 16px;

  font-size: 14px;
  line-height: 17px;
  color: $dustyGray;
}

.compare-item__title-link {
  margin-bottom: 20px;

  text-decoration: none;
  color: $black;

  &:hover,
  &:focus {
    color: $fountainBlue;
  }
}

.compare-item__title {
  margin: 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.compare-item__new-price {
  margin-right: 10px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}

.compare-item__old-price {
  font-size: 14px;
  line-height: 17px;
  color: $dustyGray;

  text-decoration: line-through;
}

.compare-item__price {
  margin-bottom: 12px;
}

.compare-item__actions {
  display: flex;
}

.compare-item__add-to-cart {
  padding: 0;

  margin-right: 20px;

  width: 114px;
  height: 37px;

  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: $white;

  background-color: $black;
  border: none;
  border-radius: 10px;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $fountainBlue;
  }
}

.compare-item__add-to-cart img {
  display: none;
}

.compare-item__buy {
  display: flex;
  align-items: center;
  padding: 0;
  padding-top: 5px;

  width: 124px;
  height: 37px;

  font-weight: 600;
  font-size: 12px;
  line-height: 15px;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $fountainBlue;
  }
}

.compare-item__buy img {
  margin-right: 9px;
}

.compare-item__close-button {
  position: absolute;
  top: 12px;
  right: 0;

  width: 16px;
  height: 16px;

  padding: 0;

  border: none;
  background-color: transparent;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

@media (max-width: $desktop-l-width) {
  .compare-item {
    flex-direction: column;

    max-width: 282px;

    margin-right: 48px;
    padding-top: 5px;
  }

  .compare-item__content {
    padding-left: 20px;
  }

  .compare-item__image-link {
    justify-content: center;
    margin-bottom: 18px;
  }

  .compare-item__close-button {
    top: 6px;
  }

  .compare__controls {
    margin-bottom: 35px;
  }
}

@media (max-width: $desktop-m-width) {
  .compare-item {
    max-width: 262px;
    margin-right: 0;
  }

  .compare-item__image-link {
    padding-left: 30px;
    justify-content: flex-start;
  }

  .compare-item__content {
    padding-left: 30px;
  }

  .compare-item__title {
    max-width: 210px;
    margin-bottom: 18px;
  }

  .compare-item__close-button {
    top: 7px;
    right: -4px;
  }

  .compare-item__actions {
    flex-direction: column-reverse;
  }

  .compare-item__price {
    margin-bottom: 10px;
  }

  .compare-item__buy {
    padding-top: 0;
    margin-bottom: 7px;
  }

  .compare-item__add-to-cart {
    margin-right: 0;
  }
}

@media (max-width: $tablet-width) {
  .compare-item {
    max-width: 175px;
  }

  .compare-item__image-link {
    padding-left: 0;
    margin-right: 0;
  }

  .compare-item__content {
    padding-left: 0;
  }

  .compare-item__close-button {
    top: 5px;
    right: 5px;
  }

  .compare-item__title-link {
    margin-bottom: 15px;
  }
}

@media (max-width: $mobile-width) {
  .compare-item__img {
    width: 86px;
    height: 117px;
  }

  .compare-item {
    max-width: 130px;
  }

  .compare-item__image-link {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 130px;
    height: 130px;

    margin-bottom: 12px;
  }

  .compare-item__close-button {
    top: 5px;
    right: 0;

    width: 12px;
    height: 12px;
  }

  .compare-item__close-button img {
    width: 12px;
    height: 12px;
  }

  .compare-item__title {
    font-size: 14px;
    line-height: 17px;  
  }

  .compare-item__price {
    display: flex;
    flex-direction: column;

    margin-bottom: 13px;
  }

  .compare-item__new-price {
    font-size: 16px;
    line-height: 20px;
  }

  .compare-item__old-price {
    font-size: 14px;
    line-height: 17px;
  }

  .compare-item__add-to-cart {
    width: 53px;
    height: 33px;
  }

  .compare-item__add-to-cart span {
    display: none;
  }

  .compare-item__add-to-cart img {
    display: inline;
  }

  .compare-item__buy {
    display: none;
  }
}