.breadcrumbs {
  margin: 31px 0 32px;
}

.breadcrumbs__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.breadcrumbs__item,
.breadcrumbs__item a {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    position: relative;
    color: $dustyGray;
    text-decoration: none;
}

.breadcrumbs__item a {
  &:hover,
  &:focus {
    color: $fountainBlue;
  }
}

.breadcrumbs__item {
  margin-right: 35px;
}

.breadcrumbs__item:not(:last-child):after {
  content: "/";
  position: absolute;
  right: -20px;
  top: 2px;
  color: $mercury;
}

@media (max-width: $desktop-m-width) {
  .breadcrumbs {
    margin: 37px 0 35px;
  }
}

@media (max-width: $tablet-width) {
  .breadcrumbs__item,
  .breadcrumbs__item a {
    font-size: 12px;
    line-height: 15px;
  }
}

@media (max-width: $mobile-width) {
  .breadcrumbs {
    margin-top: 30px;
    margin-bottom: 15px;
  }
}