.compare__container {
  box-sizing: border-box;
  padding: 40px 30px;
  padding-bottom: 0;

  border-radius: 10px;
  background-color: $white;
}

.compare__title {
  margin: 0 0 30px;

  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
}

.compare__content-wrapper {
  padding-top: 30px;
  background-color: $wildSand;
}

.compare__header-wrapper {
  display: flex;
  padding-bottom: 28px;

  border-bottom: 1px solid $mercury;
}

.compare__items-wrapper {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  max-width: 1040px;
}

.compare__summary {
  align-self: center;
  max-width: 1100px;

  margin-left: 30px;
  margin-bottom: 32px;

  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}

.compare__controls {
  align-self: center;
  margin-bottom: 60px;
}

.compare__reset,
.compare__to-catalog {
  height: 50px;
  padding: 0 40px;

  font-weight: 500;
  font-size: 16px;
  line-height: 50px;

  border: none;
  border-radius: 10px;

  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $fountainBlue;
  }
}

.compare__reset {
  margin-right: 25px;
  background-color: $wildSand;
}

.compare__to-catalog {
  position: relative;

  padding-left: 55px;
  padding-right: 45px;

  color: $white;
  background-color: $black;

  &::before {
    content: url("../img/svg/icon-arrow-left.svg");
    position: absolute;

    top: 3px;
    left: 40px;
  }
}

@media (max-width: $desktop-l-width) {
  .compare__container {
    padding: 45px 30px;
  }

  .compare__header-wrapper {
    padding-bottom: 50px;
  }

  .compare__summary {
    max-width: 992px;

    margin-left: 0;
    margin-bottom: 38px;
    padding-top: 20px;

    font-size: 18px;
    line-height: 22px;
  }
}

@media (max-width: $desktop-m-width) {
  .compare__content-wrapper {
    padding-top: 45px;
  }

  .compare__container {
    padding: 40px 30px;
  }

  .compare__header-wrapper {
    padding-bottom: 40px;
  }

  .compare__summary {
    padding-top: 15px;
  }

  .compare__controls {
    margin-bottom: 30px;
  }
}

@media (max-width: $tablet-width) {
  .compare__container {
    padding: 42px 21px 42px 26px; 
  }

  .compare__title {
    font-size: 30px;
    line-height: 37px;
    margin: 0 0 22px;
  }

  .compare__content-wrapper {
    padding-top: 30px;
  }

  .compare__items-wrapper {
    max-width: 400px;
  }

  .compare__summary {
    max-width: 596px;

    padding-top: 20px;

    margin-bottom: 18px;

    font-size: 16px;
    line-height: 20px;
  }

  .compare__controls {
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-width) {
  .compare__title-wrapper {
    padding: 0 30px;
  }

  .compare__title {
    text-align: center;
    font-size: 28px;
    line-height: 34px;
  }

  .compare__header-wrapper {
    flex-direction: column;
    padding-bottom: 33px;
  }

  .compare__content-wrapper {
    padding-top: 0;
    background-color: $white;
  }

  .compare__container {
    padding: 18px 19px 42px 19px;

    border-radius: 0;
  }

  .compare__summary {
    padding-top: 8px;
    margin-bottom: 45px;

    font-size: 12px;
    line-height: 15px;
    text-align: center;
  }

  .compare__controls {
    display: flex;
    flex-direction: column;
  }

  .compare__reset {
    margin-right: 0;
    margin-bottom: 8px;

    text-align: left;
  }
}